/*
 http([config])
   + url 
   + method   *GET/DELETE/HEAD/OPTIONS/POST/PUT/PATCH
   + credentials   *include/same-origin/omit
   + headers:null 
   + body:null POSTurlencoded(Content-Type)...
   + params:null url
   + responseType   *json/text/arrayBuffer/blob
   + signal 
 -----
 http.get/head/delete/options([url],[config])  url/method
 http.post/put/patch([url],[body],[config])  url/method/body
 */

import _ from "../assets/utils";
import qs from "qs";
import { message } from "antd";

/**
 * 
 */

const http = function http(config) {
  // initial config & validate
  if (!_.isPlainObject(config)) config = {};
  config = Object.assign(
    {
      url: "",
      method: "GET",
      credentials: "include",
      headers: null,
      body: null,
      params: null,
      responseType: "json",
      signal: null,
    },
    config
  );
  if (!config.url) throw new TypeError("url must be required");
  if (!_.isPlainObject(config.headers)) config.headers = {};
  if (config.params !== null && !_.isPlainObject(config.params))
    config.params = null;
  console.log(config, "fff");
  /**
   * 
   */
  let {
    url,
    method,
    credentials,
    headers,
    body,
    params,
    responseType,
    signal,
  } = config;
  // 1. 
  if (params) {
    url += `${url.includes("?") ? "&" : "?"}${qs.stringify(params)}`;
  }
  // : urlencoded(Content-Type)
  if (_.isPlainObject(body)) {
    body = qs.stringify(body);
    headers['Content-Type'] = 'application/x-www-form-urlencoded';
  }

  // axios
  let token = localStorage.getItem("auth");
  if (token) {
    headers["Authorization"] = token;
  }

  method = method.toUpperCase();

  config = {
    method,
    credentials,
    headers,
    caches: "no-cache",
    signal,
  };

  if (/^(POST|PUT|PATCH)$/.test(method) && body) config.body = body;

  return fetch(url, config)
    .then((response) => {
      const { status, statusText } = response;
      if (/^(2|3)\d{2}$/.test(status)) {
        let result;
        switch (responseType.toLowerCase()) {
          case "text":
            result = response.text();
            break;
          case "arrayBuffer":
            result = response.arrayBuffer();
            break;
          case "blob":
            result = response.blob();
            break;
          default:
            result = response.json();
            break;
        }

        return result;
      }

      return Promise.reject({
        code: -100,
        status,
        statusText,
      });
    })
    .catch((reason) => {
      if (reason && typeof reason === "object") {
        const { code, status, error } = reason;
        if (code === -100) {
          switch (+status) {
            case 400:
              message.error("");
              break;

            default:
              message.error(error);
              break;
          }
        } else {
        }
      } else {
        message.error("");
      }
      return Promise.reject(reason);
    });
};

/**
 * 
 */
["GET", "HEAD", "DELETE", "OPTIONS"].forEach((item) => {
  http[item.toLowerCase()] = function (url, config) {
    if (!_.isPlainObject(config)) config = {};
    config["url"] = url;
    config["method"] = item;
    return http(config);
  };
});

["POST", "PUT", "PATCH"].forEach((item) => {
  http[item.toLowerCase()] = function (url, body, config) {
    if (!_.isPlainObject(config)) config = {};
    config["url"] = url;
    config["body"] = body;
    config["method"] = item;
    return http(config);
  };
});

export default http;
