import { useSelector } from "react-redux";

const VoteMain = function VoteMain(props) {
  const { supNum, oppNum } = useSelector((state) => state.vote );
  return (
    <div className="main">
      <div className="line">{supNum} </div>
      <div className="line opp">{oppNum} </div>
    </div>
  );
};

export default VoteMain;
