/**
 * Vote
 * reducer&actionCreator
 */

import { createSlice } from "@reduxjs/toolkit";

const voteSlice = createSlice({
  name: "vote",
  initialState: {
    supNum: 0,
    oppNum: 0,
  },
  // 
  reducers: {
    addSup(state, { payload }) {
      console.log(payload, 'payload');
      state.supNum++;
    },
    addOpp(state, action) {
      state.oppNum++;
    },
  },
});

export let { addSup, addOpp } = voteSlice.actions;

export default voteSlice.reducer;
