import {
  Button,
  Tag,
  Table,
  Modal,
  Form,
  Input,
  DatePicker,
  Popconfirm,
  message,
} from "antd";
import React, { useState, useEffect } from "react";
import { getTaskList, addTask, removeTask, completeTask } from "@/api";
import "./index.less";
import { flushSync } from "react-dom";

// textrea
const { TextArea } = Input;

//  yyyy-MM-dd HH:mm:ss -> MM-dd HH:mm:ss
const zero = function (text) {
  text = String(text);
  return text.length < 2 ? "0" + text : text;
};
const formatTime = function (time) {
  let arr = time.match(/\d+/g),
    [, month, day, hours = "00", minutes = "00"] = arr;
  return `${zero(month)}-${zero(day)} ${zero(hours)}:${zero(minutes)}`;
};

const TaskTestOfFun = function () {
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [tableLoading, setTableLoading] = useState(false);
  const [confirmLoading, setConfirmLoading] = useState(false);
  const [selectedIndex, setSelectedIndex] = useState(0); // 
  const [tableData, setTableData] = useState([]);

  // let formRef = useRef(null);
  const [formIns] = Form.useForm();

  const columns = [
    {
      title: "",
      dataIndex: "id",
      align: "center",
      width: "8%",
    },
    {
      title: "",
      dataIndex: "task",
      ellipsis: true,
      width: "50%",
    },
    {
      title: "",
      dataIndex: "state",
      render: (text) => (text === 2 ? "" : ""),
      align: "center",
    },
    {
      title: "",
      dataIndex: "time",
      align: "center",
      render: (_, record) => {
        let { state, time, complete } = record;
        if (+state === 2) time = complete;

        return formatTime(time);
      },
    },
    {
      title: "",
      key: "action",
      render: (_, record) => {
        const { state, id } = record;
        return (
          <>
            <Popconfirm
              title="?"
              onConfirm={handleDel.bind(null, id)}
              okText=""
              cancelText=""
            >
              <Button type="link"></Button>
            </Popconfirm>

            {state === 1 ? (
              <Button type="link" onClick={completeHandle.bind(null, id)}>
                
              </Button>
            ) : null}
          </>
        );
      },
    },
  ];

  const handleChange = (index) => {
    // if (index === selectedIndex) return;
    flushSync(() => {
      setSelectedIndex(index);
    });

    query();
  };

  const handleOk = async () => {
    try {
      await formIns.validateFields();
      let { task, time } = formIns.getFieldsValue();
      time = time.format("YYYY-MM-DD HH:mm:ss"); // Form
      setConfirmLoading(true);
      const { code } = await addTask(task, time);
      if (+code !== 0) {
        message.error("");
      } else {
        message.success("");
      }

      setIsModalOpen(false);

      query();
    } catch (_) {}
    setConfirmLoading(false);
  };

  const handleCancel = () => {
    setConfirmLoading(false);
    setIsModalOpen(false);
    formIns.resetFields();
  };

  const handleDel = async (id) => {
    try {
      await removeTask(id);
      query();
    } catch (_) {}
  };

  const completeHandle = async (id) => {
    try {
      await completeTask(id);
      query();
    } catch (_) {}
  };
  const query = async () => {
    setTableLoading(true);
    try {
      let { code, list } = await getTaskList(selectedIndex);
      if (+code !== 0) list = [];
      setTableData(list);
    } catch (_) {}
    setTableLoading(false);
  };

  useEffect(() => {
    query();
  }, [selectedIndex]);

  return (
    <>
      <div className="task-box">
        <div className="header">
          <h2>TASK OA </h2>
          <Button
            type="primary"
            onClick={() => {
              setIsModalOpen(true);
            }}
          >
            
          </Button>
        </div>
        <div className="btns">
          {["", "", ""].map((item, index) => {
            return (
              <Tag
                key={index}
                color={selectedIndex === index ? "#1677ff" : ""}
                onClick={handleChange.bind(null, index)}
              >
                {item}
              </Tag>
            );
          })}
        </div>
        <div className="table-wrap">
          <Table
            columns={columns}
            rowKey="id"
            dataSource={tableData}
            loading={tableLoading}
            pagination={false}
          />
        </div>
        <Modal
          title=""
          destroyOnClose
          confirmLoading={confirmLoading}
          open={isModalOpen}
          onOk={handleOk}
          onCancel={handleCancel}
        >
          <Form
            form={formIns}
            layout="vertical"
            autoComplete="off"
            validateTrigger="onBlur"
          >
            <Form.Item
              label=""
              name="task"
              rules={[
                {
                  required: true,
                  message: "",
                },
                {
                  min: 6,
                  message: "6",
                },
              ]}
            >
              <TextArea autoSize={{ minRows: 2, maxRows: 6 }} />
            </Form.Item>
            <Form.Item
              label=""
              name="time"
              rules={[
                {
                  required: true,
                  message: "",
                },
              ]}
            >
              <DatePicker className="date-pick" showTime />
            </Form.Item>
          </Form>
        </Modal>
      </div>
    </>
  );
};

export default TaskTestOfFun;
